export default{
    data(){
        return{
            msg:"这是首页",
            env:2,
            selectList:[
                {
                    label:'正式环境',
                    value:1
                },
                {
                    label:'测试环境',
                    value:2
                },
            ],
            siteList:[],//所有的网站列表
            showList:[],//显示的网站列表
            listShow:true,//显示网站列表
            rounds:[],//外面的小圆点
            roundIndex:1,
            roundShow:false,
            floderList:[],//文件夹的网站列表
            showFloderList:[],//文件夹显示的网站列表
            floderRounds:[],//文件夹里面的小圆点
            floderShow:false,
            floderTitle:"",//文件夹里面的title
            floderIndex:1,
            floderListShow:true,//显示文件里的网站列表
            folderDetail:{
                width:'0rem',
                height:'0rem',
                startx:0,
                starty:0,
                x:0,
                y:0
            }
        }
    },
    methods:{
        //选择正式环境还是测试环境
        chooseEnv(item){
            this.value = item.value;
            this.getSites(this.value);
        },
        //获取网站列表
        async getSites(value){
            let last;
            if(value == 1){
                last = await this.$http.get(this.$URL + 'formal-environment.json');
            }else{
                last = await this.$http.get(this.$URL + 'test-environment.json');
            }
            if(last.data.success){
                this.siteList = last.data.data;
                this.showList = this.siteList.slice(0,10);
                this.renderRound(this.siteList);
            }else{
                this.siteList = [];
            }
        },
        //渲染外边列表小圆点
        renderRound(list){
            this.rounds = [];
            let number = Math.ceil(list.length/10);
            for(let i = 1;i <= number;i++){
                this.rounds.push(i);
            }
            if(this.rounds.length > 1){
                this.roundShow = true;
            }else{
                this.roundShow = false;
            }
        },
        //点击小圆点触发事件
        clickRound(index){
            this.roundIndex = index + 1;
            this.listShow = false;
            this.roundShow = true;
            setTimeout(() => {
                this.showList = this.siteList.slice((this.roundIndex - 1) * 10,(this.roundIndex) * 10);
                this.listShow = true;
            },400);
        },
        //点击外网链接跳转外网
        jumpLink(item){
            if(item.url){
                window.open(item.url);
            }else{
                this.$message({
                    type:"warning",
                    message:item.name + "网站没有配置url"
                })
            }
        },
        //点击文件夹div出现文件夹详情
        showFloder(item){
            this.folderDetail.y = event.x;
            this.folderDetail.x = event.y;
            this.folderDetail.starty = event.x;
            this.folderDetail.startx = event.y;
            this.floderList = item.childrens;
            this.floderTitle = item.name;
            this.floderShow = true;
            this.roundShow = false;
            setTimeout(() => {
                this.folderDetail.width = '11rem';
                this.folderDetail.height = '6rem';
                this.showFloderList = this.floderList.slice(0,8);
                this.renderFolderRound(this.floderList);
                this.folderDetail.y = window.innerWidth/2;
                this.folderDetail.x = window.innerHeight/2;
                setTimeout(() => {
                    document.querySelector('.home .content .floder-mock .floder-box').style.overflow = 'visible';
                },300);
            },41);
        },
        //关闭文件夹弹窗
        closeFloder(){
            document.querySelector('.home .content .floder-mock .floder-box').style.overflow = 'hidden';
            this.folderDetail.y = this.folderDetail.starty;
            this.folderDetail.x = this.folderDetail.startx;
            this.folderDetail.width = '0rem';
            this.folderDetail.height = '0rem';
            if(this.rounds.length > 1){
                this.roundShow = true;
            }else{
                this.roundShow = false;
            }
            setTimeout(() => {
                this.floderShow = false;
            },300);
        },
        //渲染文件夹列表小圆点
        renderFolderRound(list){
            this.floderRounds = [];
            let number = Math.ceil(list.length/8);
            for(let i = 1;i <= number;i++){
                this.floderRounds.push(i);
            }
        },
        //点击小圆点
        clickFolderRound(index){
            this.floderIndex = index + 1;
            this.floderListShow = false;
            setTimeout(() => {
                this.showFloderList = this.floderList.slice((this.floderIndex - 1) * 8,(this.floderIndex) * 8);
                this.floderListShow = true;
            },1200);
        }
    },
    mounted(){
        this.getSites(this.env);
    }
}


