import Vue from 'vue'
import App from './App'
import router from './router'
import axios from 'axios';
import Qs from 'qs';
import cookie from './server/cookie';
import './components/global.js';
import url from "./server/url";
Vue.config.productionTip = false;
Vue.prototype.$http = axios;
Vue.prototype.$Qs = Qs;//this.$Qs.stringify(params);
Vue.prototype.$cookie = cookie;
Vue.prototype.$URL = url.URL;
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
Vue.use(ElementUI);
axios.interceptors.request.use(function(config){//请求拦截器
    //请求之前做点什么
    return config;
},function (error) {
    // 对请求错误做些什么
    return Promise.reject(error);
});
function resetRem(){
    let width = window.innerWidth;
    if(width < 1400){
        var designW = 1650;
        var font_rate = 100;
        var font_size = width/designW * font_rate;
        document.querySelector('html').style.fontSize = font_size + 'px';
    }else{
        document.querySelector('html').style.fontSize = '625%';
    }
}
resetRem();
window.onresize = function(){
    resetRem();
}
new Vue({
    el:"#app",
    router,
    components:{App},
    template: '<App/>'
})



